.scaleIn {
    -webkit-animation: scaleIn .2s ease forwards;
            animation: scaleIn .2s ease forwards;
}

.scaleOut {
    -webkit-animation: scaleOut .2s ease forwards;
            animation: scaleOut .2s ease forwards;
}

.slideUp {
    -webkit-animation: slideUp .2s ease forwards;
            animation: slideUp .2s ease forwards;
}

.shake {
    -webkit-animation: shake .5s ease forwards;
            animation: shake .5s ease forwards;
}

@-webkit-keyframes scaleIn {
    from {
        -webkit-transform: scale(0);
        transform: scale(0);
        display: none;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        display: inline;
    }
}

@keyframes scaleIn {
    from {
        -webkit-transform: scale(0);
        transform: scale(0);
        display: none;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        display: inline;
    }
}

@-webkit-keyframes scaleOut {
    from {
        -webkit-transform: scale(1);
        transform: scale(1);
        display: inline;
    }
    to {
        -webkit-transform: scale(0);
        transform: scale(0);
        display: none;
    }
}

@keyframes scaleOut {
    from {
        -webkit-transform: scale(1);
        transform: scale(1);
        display: inline;
    }
    to {
        -webkit-transform: scale(0);
        transform: scale(0);
        display: none;
    }
}

@-webkit-keyframes slideUp {
    from {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    to {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }
}

@keyframes slideUp {
    from {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    to {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }
}

@-webkit-keyframes shake {
    10%, 90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }
    20%, 80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }
    30%, 50%, 70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }
    40%, 60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}

@keyframes shake {
    10%, 90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }
    20%, 80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }
    30%, 50%, 70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }
    40%, 60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}
